.card__text {
  display: flex;
  flex: 1;

  align-items: center;
}

.right__text {
  font-size: 13px;
  margin: 1px;
  display: flex;
  flex: 0.5;
  justify-content: flex-end;
  color: white;
}
.left__text {
  font-size: 13px;
  margin: 1px;
  color: white;
  display: flex;
  flex: 0.5;
  justify-content: flex-start;
}
.bottom__text {
  font-size: 13px;
  font-family: "Times New Roman", Times, serif;
  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: center;
}
.left__bottomtext {
  display: flex;
  flex: 0.2;
  justify-content: flex-start;
  margin-left: 2px;
}
.right__bottomtext {
  display: flex;
  flex: 0.8;
  justify-content: flex-end;
  margin-right: 2px;
}
.text-muted {
  background-color: azure;
  display: flex;
  flex-direction: row;
  flex: 1;
  align-items: center;
  justify-content: center;
}
.text-muted > div {
  margin: 2px;
  padding: 2px;
}
.item {
  width: 100%;

  margin: 2px;
}

.multi__items {
  display: flex;
  height: 80%;
  flex-wrap: wrap;
  background-color: azure;
}

.multi__items > div {
  flex: 48%; /* or - flex: 0 50% - or - flex-basis: 50% - */
  /*demo*/
}
.home_image {
margin-top: 2rem;
  background-image: linear-gradient(orange, white);
    width: 100%;
    height: auto;
    background-size: cover;
 

}

